import { icon } from "@fortawesome/fontawesome-svg-core/import.macro";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Button, Card, CloseButton, Form, InputGroup } from "react-bootstrap";
import { KeyValuePair, RemoveItem } from "../../Models/Ocelot/KeyValuePair";
import { useState } from "react";

export type TabularDataProps = {
    data: KeyValuePair[],
    title?: string,
    placeholder1?: string,
    placeholder2?: string,
    onAdd: ()=>void;
    onItemChange:(i:number, key:string, text:string)=>void;
    onRemove: (i:number)=>void;
}

export default function TabularData(props: TabularDataProps ){
    return (
        <Card className="p-0 mb-3">
            <Card.Header className="d-flex align-items-center justify-content-between">
                <Card.Title as="h6" className="mb-0 ">{props?.title ?? ""}</Card.Title>
                <Button variant="success" size="sm" 
                onClick={props.onAdd}
                >
                    <FontAwesomeIcon icon={icon({ name: 'plus' })} />
                </Button>
            </Card.Header>
            <Card.Body className="p-0 m-0">
            {
                props.data.length > 0 && props.data.map((keyVal, i)=>(
                    <RowData2 key={i} hideBorder={i == 0} 
                    placeholder1={props.placeholder1}
                    placeholder2={props.placeholder2}
                    data={keyVal} 
                    onRemove={()=>props.onRemove(i)}
                    onChange={(type,val)=>props.onItemChange(i, type,val)}
                    />
                ))
            }
            {
                props.data.length == 0 && <InputGroup className="p-1 ps-3">No data</InputGroup>
            }
            </Card.Body>
        </Card>
    )
}

function RowData2(props: {data:KeyValuePair, placeholder1?: string, placeholder2?: string, hideBorder: boolean,
     onRemove:(()=>void),
     onChange: ((type:string, value:string)=>void)}) {
    let classNames= "border-0 d-flex align-items-center";
    classNames += props.hideBorder ? "" : " border-top";
    return (
        <InputGroup className={classNames} >
            <Form.Control placeholder={props.placeholder1} className="border-0 border-end"  value={props.data.Key} 
            onChange={(e)=> props.onChange('col1', e.target.value)}
            />
            <InputGroup.Text className="border-0" >
                <FontAwesomeIcon icon={icon({ name: 'arrow-right' })} />
            </InputGroup.Text>
            <Form.Control value={props.data.Value} placeholder={props.placeholder2} className="border-0 border-start border-end"  
            onChange={(e)=> props.onChange('col2',e.target.value)}
            />
            <CloseButton className="mx-2" onClick={props.onRemove}  />
        </InputGroup>
    )
}