import { icon } from "@fortawesome/fontawesome-svg-core/import.macro";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState } from "react";
import { Button, Col, Form, Modal } from "react-bootstrap";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { HttpHandlerOptions } from "../../Models/Ocelot/HttpHandlerOptions";

export default function HttpHandlerOptionsComponent(props:BaseProps<HttpHandlerOptions>) {
    const [opt, setOpt] = useState(props.data);
    const [show, setShow] = useState(false);
    const handleClose = () => {
        setOpt(props.data);
        setShow(false);
    }
    const handleShow = () => setShow(true);
    
    const onChange = (key:string, val:any) => {
        setOpt({
            ...opt,
            [key]: val
        });
    }
    const emitChange = () => {
        props.onChange && props.onChange(opt??{});
        setShow(false);
    }
    return (
        <>
            <Button variant="outline-light" className="border-0 m-0"  onClick={() => handleShow()}>
                <FontAwesomeIcon className="text-primary" icon={icon({ name: 'arrow-right-arrow-left' })}></FontAwesomeIcon>
            </Button>
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Http Handler options</Modal.Title>
                </Modal.Header>
                <Modal.Body >
                    <Col sm="6">
                    <Form.Check type="switch" checked={opt?.AllowAutoRedirect??false} label="AllowAutoRedirect" onChange={e=> onChange('AllowAutoRedirect',e.target.checked)}/>
                    <Form.Check type="switch" checked={opt?.UseCookieContainer??false} label="UseCookieContainer"  onChange={e=> onChange('UseCookieContainer',e.target.checked)}/>
                    <Form.Check type="switch" checked={opt?.UseTracing??false} label="UseTracing"  onChange={e=> onChange('UseTracing',e.target.checked)}/>
                    <Form.Check type="switch" checked={opt?.UseProxy??false} label="UseProxy"  onChange={e=> onChange('UseProxy',e.target.checked)}/>
                    </Col>
                    <Form.Label>MaxConnectionsPerServer</Form.Label>
                    <Form.Control type="number" value={opt?.MaxConnectionsPerServer??""} onChange={e=> onChange('MaxConnectionsPerServer',e.target.value)}/>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Cancel
                    </Button>
                    <Button variant="primary" onClick={emitChange}>
                        Ok
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}