import { FileConfiguration } from "./Ocelot/FileConfiguration";
import { FileRoute } from "./Ocelot/FileRoute";
import { Dictionary } from "./Ocelot/KeyValuePair";

export function Idify(config: FileConfiguration) {
  (config.Routes ?? []).forEach((x, i) => {
    x.__id = i.toString()
  });
  return config;
}

export function GroupByPath(routes:FileRoute[]): {
  SortedKeys:string[],
  Items: Dictionary<FileRoute[]>
}
  {
  let items: Dictionary<FileRoute[]> = {};
    let rt = routes ?? [];
    for (let r of rt) {
        let title = (r.UpstreamPathTemplate ?? "").split('/')[1];
        if (!Object.keys(items).includes(title)) {
            items[title] = [];
        }
        items[title].push(r);
    }
    let keys = Object.keys(items).sort();
    return {
      SortedKeys: keys,
      Items: items
    }
}