import { Form, FormLabel } from "react-bootstrap";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { FileServiceDiscoveryProvider } from "../../Models/Ocelot/FileServiceDiscoveryProvider";


export default function ServiceDiscoveryProviderComponent(props:BaseProps<FileServiceDiscoveryProvider>){
    const onChange = (key: string, val:any) => {
        props.onChange && props.onChange({
            ...props.data, [key]: val
        });
    }
    return (
        <>
            <FormLabel>Scheme</FormLabel>
            <Form.Control value={props.data?.Scheme ?? ""}
                onChange={(e)=> onChange('Scheme', e.target.value)} ></Form.Control>
            <FormLabel className="mt-2">Host</FormLabel>
            <Form.Control value={props.data?.Host ?? ""}
                onChange={(e)=> onChange('Host', e.target.value)} ></Form.Control>
            <FormLabel className="mt-2">Port</FormLabel>
            <Form.Control value={props.data?.Port ?? ""} type="number"
                onChange={(e)=> onChange('Port', e.target.value)} ></Form.Control>
            <FormLabel className="mt-2">PollingInterval</FormLabel>
            <Form.Control value={props.data?.PollingInterval ?? ""} type="number"
                onChange={(e)=> onChange('PollingInterval', e.target.value)} ></Form.Control>
            <FormLabel className="mt-2">Token</FormLabel>
            <Form.Control value={props.data?.Token ?? ""}
                onChange={(e)=> onChange('Token', e.target.value)} ></Form.Control>
            <FormLabel className="mt-2">Type</FormLabel>
            <Form.Control value={props.data?.Type ?? ""}
                onChange={(e)=> onChange('Type', e.target.value)} ></Form.Control>
        </>
    )
}