import { icon } from "@fortawesome/fontawesome-svg-core/import.macro";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState } from "react";
import { Button, Form, FormLabel, Modal } from "react-bootstrap";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { LoadBalancerOptions } from "../../Models/Ocelot/LoadBalancerOptions";

export  function LoadBalancerOptionsModalComponent(props:BaseProps<LoadBalancerOptions>){
    const [show, setShow] = useState(false);
    const [opt, setOpt] = useState(props.data);
    const handleClose = () => {
        setOpt(props.data);
        setShow(false);
    }
    const handleShow = () => setShow(true);
    const onChange = (val:LoadBalancerOptions) =>setOpt(val);

    const emitChange = () => {
        props.onChange &&  props.onChange(opt??{});
        setShow(false);
    }
    return (
        <>
            <Button variant="outline-light" className="border-0" onClick={()=>handleShow()}>
                <FontAwesomeIcon className="fs-5 text-success" icon={icon({ name: 'balance-scale' })}></FontAwesomeIcon>
            </Button>
            <Modal show={show}  onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Load Balancer Options</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <LoadBalancerOptionsComponent data={opt} onChange={onChange}/>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Cancel
                    </Button>
                    <Button variant="primary" onClick={emitChange}>
                        Ok
                    </Button>
                    
                </Modal.Footer>
                    
            </Modal>
        </>
    )
}

export default function LoadBalancerOptionsComponent(props:BaseProps<LoadBalancerOptions>){
    const onChange = (key:string, val:any) => {
        if(props.onChange){
            props.onChange({
                ...props.data,
                [key]:val
            })
        }
    }
    return (
        <>
            <FormLabel>Type</FormLabel>
            <Form.Control value={props.data?.Type ?? ""}
                onChange={(e)=> onChange('Type',e.target.value)} ></Form.Control>
            <FormLabel className="mt-2">Key</FormLabel>
            <Form.Control value={props.data?.Key ?? ""}
                onChange={(e)=> onChange('Key',e.target.value)}></Form.Control>
            <FormLabel className="mt-2">Expiry</FormLabel>
            <Form.Control value={props.data?.Expiry ?? ""} type="number"
                onChange={(e)=> onChange('Expiry',e.target.value)}></Form.Control>
        </>
    )
}