import { Button, Card, CloseButton, Col, Form, InputGroup } from "react-bootstrap";
import { FileRouteDownstream } from "../../Models/Ocelot/FileRoute";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { icon } from "@fortawesome/fontawesome-svg-core/import.macro";
import { BaseProps, OnChange } from "../../Models/OcelotUi/BaseProps";
import HttpMethodComponent from "./HttpMethodComponent";

export default function DownstreamComponent(props: BaseProps<FileRouteDownstream>) {
    const onAdd = () => {
        let item = props.data?.DownstreamHostAndPorts ?? [];
        item.push({Host: "",Port: 0});
        props.onChange && props.onChange({
            ...props.data,
            DownstreamHostAndPorts : item
        })
     }
     const onRemove = (i:number) => {
        let item = [...props.data?.DownstreamHostAndPorts ?? []];
        item.splice(i,1);
        props.onChange && props.onChange({
            ...props.data,
            DownstreamHostAndPorts : item
        })
     }

     const onItemChange = (i:number, key:string, value:string) => {
        let item = props.data?.DownstreamHostAndPorts ?? [];
        if(key ==='Host'){
            item[i].Host = value;
        }
        else{
            item[i].Port = parseInt(value);
        }
        props.onChange && props.onChange({
            ...props.data,
            DownstreamHostAndPorts : item
        })
     }
     const onChange = (key: string, val:any) => {
        if(key === '') {
            props.onChange && props.onChange({
                ...props.data,
                ...val
            })
        } else {
            props.onChange && props.onChange({
                ...props.data,
                [key]: val
            });
        }
    }
   
    return (
        <>
            <Card>
                <Card.Header className="p-2 m-0">
                    <Card.Title className="d-flex align-items-center m-0">Downstream</Card.Title>
                </Card.Header>
                <Card.Body>
                    <HttpMethodComponent data={props.data?.DownstreamHttpMethod??[]} onChange={(e)=>onChange('DownstreamHttpMethod',e)}/>
                    <DownstreamPathComponent {...props} onChange={props.onChange} />
                    <HostsComponent {...props} onAdd={onAdd} onRemove={onRemove} onItemChange={onItemChange}/>
                </Card.Body>
            </Card>

        </>
    )
}

function DownstreamPathComponent(props: BaseProps<FileRouteDownstream>){
    
    const onChange = (key: string, val:any) => {
        if(props.onChange){
            props.onChange({
                ...props.data,
                [key]: val,
            });
        }
    }
    
    return (
        <InputGroup className="mb-2">
            <input className="form-control http-scheme"  value={props.data?.DownstreamScheme??""}
            onChange={(e)=> onChange('DownstreamScheme',e.target.value)}
            style={{width:"20%"}}
             /> 
             <input className="form-control http-scheme"  placeholder="Downstream path" 
              value={props.data?.DownstreamPathTemplate??""}
            onChange={(e)=> onChange('DownstreamPathTemplate',e.target.value)}
            style={{width:"80%"}}
             /> 
            
            {/* <Form.Control placeholder="Downstream path" onChange={(e)=> onChange('DownstreamPathTemplate',e.target.value)} value={props.data?.DownstreamPathTemplate} /> */}
      </InputGroup>
    )
}

function HostsComponent(props: BaseProps<FileRouteDownstream> & {onAdd:OnChange<void>, onRemove:OnChange<number>, onItemChange:Function}) {
    return (
        <Card>
            <Card.Header className="d-flex align-items-center justify-content-between">
                <Card.Title as="h6" className="mb-0">Host/Port</Card.Title>
                <Button variant="success" size="sm" className="m-0" onClick={() => props.onAdd()} >
                    <FontAwesomeIcon icon={icon({ name: 'plus' })} 
                    />
                </Button>
            </Card.Header>
            <Card.Body className="p-0">
            {
               (props.data?.DownstreamHostAndPorts??[]).map((x,i)=>(<HostComponent 
                    key={i} {...x} hideBorder={i == 0} 
                        onRemove={()=> props.onRemove(i)}
                        onChange={(key:string,value:string)=>props.onItemChange(i, key, value)}
                    />))
            }
            {
                props.data?.DownstreamHostAndPorts?.length == 0 && 
                <InputGroup className="p-1 ps-3">No host/port defined</InputGroup>
            }
            </Card.Body>
        </Card>
    );
}

function HostComponent(props: { Host: string, Port: number, hideBorder: boolean, onChange:Function, onRemove:Function }) {
    let classNames = "border-0 d-flex align-items-center";
    classNames += props.hideBorder ? "" : " border-top";
    return (
        <InputGroup className={classNames}>
            <input type="text" placeholder="Host" className="form-control border-0 border-end " style={{
                width: "45%"
            }} 
            value={props.Host} 
            onChange={(e)=> props.onChange('Host', e.target.value)}
            />
            <InputGroup.Text className="border-0">:</InputGroup.Text>
            {/* <input type="text" placeholder="Port" className="form-control border-0 border-end col-sm-8 " value={props.Host} readOnly /> */}
                <Form.Control type="number" placeholder="Port" className="border-0 border-start border-end mb-0 me-0 pe-0" 
                value={props.Port}
                onChange={(e)=> props.onChange('Port', e.target.value)}
                 />
           
            <CloseButton className="ms-2 me-2" onClick={()=>props.onRemove()} />
        </InputGroup>
    )
}
