import { Button, ToggleButtonGroup } from "react-bootstrap";
import { Colors } from "../../Models/Colors";
import { HttpMethod } from "../../Models/Ocelot/HttpMethod";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { useState } from "react";

const group1 = ["Get","Post","Put","Patch","Delete"];
const group2 = ["Head","Trace","Options","Connect"];

export default function HttpMethodComponent(props:BaseProps<HttpMethod[]>) {
    
    const val1 = (props.data ?? []).filter(x=> group1.includes(x));
    const color1 = group1.map(x=> Colors[x]);
    const val2 = (props.data ?? []).filter(x=> group2.includes(x));
    const color2 = group2.map(x=> Colors[x]);

    const onChange = (group:string, val:string[]) => {
        let vals = group === "g1" ? [...val2, ...val] : [...val1, ...val];
        props.onChange && props.onChange(vals);
    }
    return (
        <>
            <HeaderButtonGroup value={val1} items={group1} variants={color1} onChange={(e)=> onChange("g1",e)}/>
            <HeaderButtonGroup value={val2} items={group2} variants={color2}  onChange={(e)=> onChange("g2",e)}/>
        </>

    )
}
function HeaderButtonGroup(props:{value?:string[], items:string[], variants:string[], onChange?:(vals:string[])=>void}){
    
    const toggle = (val:string)=> {
        if(!props.onChange)
            return;
        if((props.value??[]).includes(val)){
            props.onChange([...(props.value??[]).filter(x=>x !== val)]);
        } else {
            props.onChange([...props.value ?? [], val]);
        }
    }
    return(
        <ToggleButtonGroup className="mb-2" type="checkbox" >
                {
                    props.items.map((item, i)=>(
                        <Button key={i} 
                        variant={(props.value??[]).includes(item) ? props.variants[i] : "outline-secondary"}
                        value={item}
                        onClick={()=>toggle(item)}
                        >{item}</Button>
                    ))
                }
        </ToggleButtonGroup>
    )
}