import { Col, Form, InputGroup, Modal } from "react-bootstrap";
import RequestTransformation from "./RequestTransformationComponent";
import ResponseTransformation from "./ResponseTransformationComponent";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { FileRoute } from "../../Models/Ocelot/FileRoute";
import UpstreamComponent from "./UpstreamComponent";
import DownstreamComponent from "./DownstreamComponent";
import DeleteButton from "../Common/DeleteButton";

export type FileRouteComponentProps = BaseProps<FileRoute> & {
    onDelete: () => void,
    showDeleteButton: boolean
}
export default function FileRouteComponent(props: FileRouteComponentProps) {
    const handleYes = () => {
        props.onDelete && props.onDelete();
    }
    const onChange = (key: string, val: any) => {
        props.onChange &&
            props.onChange({
                ...props.data,
                [key]: val
            })
    }
    return (
        <>
            <Col lg="4" className="ms-2">
                <UpstreamComponent data={props.data} onChange={props.onChange} />
                <div>
                    <Form.Check type="switch" label="Route is case sensitive" onChange={(e) => onChange('RouteIsCaseSensitive', e.target.checked)} />
                    <Form.Check type="switch" className="text-danger"
                        label="Dangerously accept any server certificate validator"
                        onChange={(e) => onChange('DangerousAcceptAnyServerCertificateValidator', e.target.checked)}
                    />
                    <InputGroup>
                        <InputGroup.Text>Key</InputGroup.Text>
                        <Form.Control onChange={(e)=>onChange('Key', e.target.value)} />
                    </InputGroup>
                </div>
                <div className="mt-3 d-flex justify-content-between">
                    {
                        props.showDeleteButton && 
                        <DeleteButton 
                            title="Delete??"
                            description="Do you want to delete this route?" 
                            onDelete={handleYes}
                        />
                    }
                </div>
            </Col>
            <Col>
                <RequestTransformation data={props.data} onChange={props.onChange} />
                <ResponseTransformation data={props.data} onChange={props.onChange} />
            </Col>
            <Col lg="4">
                <DownstreamComponent data={props.data} onChange={props.onChange} />
            </Col>
        </>
    )
}