export type Dictionary<TValue> = { [Key: string]: TValue;};
export type KeyValuePair = {
    Key:string,
    Value:string
}



export const DictionaryToKeyValuePairs = (data:Dictionary<string>) : KeyValuePair[] => {
    
    let keys = Object.keys(data??{});
    if(keys.length === 0) return [];
    let keyVals = new Array<KeyValuePair>();
    keys.forEach(x=> {
        keyVals.push({Key: x, Value: data[x]});
    })   
    return keyVals;
}

export const KeyValuePairsToDictionary = (data:KeyValuePair[]) : Dictionary<string> => {
    if(data?.length === 0) return {};
    let obj = {} as Dictionary<string>;
    data.forEach(x=> {
        obj[x.Key] = x.Value;
    });
    return obj;
}

export const RemoveItem = (items:any[], i:number) =>{
    let prevData = [...items];
    prevData.splice(i,1);
    return prevData;
}