import { icon } from "@fortawesome/fontawesome-svg-core/import.macro";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState } from "react";
import { Button, Form, FormLabel, Modal } from "react-bootstrap";
import { BaseProps, OnChange } from "../../Models/OcelotUi/BaseProps";
import { RateLimitRule } from "../../Models/Ocelot/RateLimitRule";
import ArrayControl from "../Common/ArrayControl";

export default function RateLimitRuleModalComponent(props: BaseProps<RateLimitRule>) {
    const [opt, setOpt] = useState(props.data);
    const [show, setShow] = useState(false);
    const handleClose = () => {
        setOpt(props.data);
        setShow(false); 
    }
    const handleShow = () => setShow(true);
    const emitChange  = ()=> {
        props.onChange && props.onChange(opt??{});
        setShow(false);
    }

    const onAdd = () => {
        let item = {...opt??{}};
        if(!item)
            item ={};
        if(!item?.ClientWhitelist)
            item.ClientWhitelist =[];
        item.ClientWhitelist.push("");
        setOpt(item);
     }
     const onRemove = (i:number) => {
        let item = {...opt ?? {}}
        if(!item?.ClientWhitelist)
            return;
        item.ClientWhitelist?.splice(i,1);
        setOpt(item);
     }

     const onItemChange = (i:number, val:string) => {
        let item ={... opt ?? {}};
        if(!item?.ClientWhitelist)
            return;
        item.ClientWhitelist[i] = val;
        setOpt(item);
     }

     const onDataChange = (key:string,val:any)=>{
        setOpt({
            ...opt,
            [key]: val
        })
     }

    return (
        <>
            <Button variant="outline-light" className="border-0" onClick={() => handleShow()}>
                <FontAwesomeIcon className="fs-5 text-success" icon={icon({ name: 'gauge-high' })}></FontAwesomeIcon>
            </Button>
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Rate limiting Options</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <RateLimitRuleComponent data={opt}
                    onAdd={onAdd}
                    onItemChange={onItemChange}
                    onRemove={onRemove}
                    onDataChange={onDataChange}
                    />
                </Modal.Body>
                <Modal.Footer>
                <Button variant="secondary" onClick={handleClose}>
                    Cancel
                </Button>
                <Button variant="primary" onClick={emitChange}>
                    Ok
                </Button>

                </Modal.Footer>
            </Modal>
        </>
    )
}

export function RateLimitRuleComponent(props: BaseProps<RateLimitRule>& {
    onRemove: OnChange<number>,
    onAdd: OnChange<void>,
    onItemChange: (i:number, val:string)=>void,
    onDataChange: (key:string, val:any)=>void
    }) {
    return (
        <>
            <Form.Check checked={props.data?.EnableRateLimiting ?? false} type="switch" label="EnableRateLimiting"
            onChange={e=> props.onDataChange('EnableRateLimiting',e.target.checked)}
             />
            <FormLabel className="mt-2">Period</FormLabel>
            <Form.Control value={props.data?.Period ?? ""}
            type="number"
            onChange={e=>props.onDataChange('Period',e.target.value)}
            ></Form.Control>
            <FormLabel className="mt-2">PeriodTimespan</FormLabel>
            <Form.Control value={props.data?.PeriodTimespan ?? ""}
            type="number"
            onChange={e=>props.onDataChange('PeriodTimespan',e.target.value)}
            ></Form.Control>
            <FormLabel className="mt-2">Limit</FormLabel>
            <Form.Control value={props.data?.Limit ?? ""} className="mb-2"
            type="number"
            onChange={e=>props.onDataChange('Limit',e.target.value)}
            ></Form.Control>
            <ArrayControl title="ClientWhitelist" data={props.data?.ClientWhitelist}
            placeholder="enter client to white list"
            onDelete={props.onRemove}
            onAdd={props.onAdd}
            onChange={props.onItemChange}
            />
        </>
    )
}