import { icon } from "@fortawesome/fontawesome-svg-core/import.macro";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState } from "react";
import { Button, Modal } from "react-bootstrap";
import TabularData from "../Common/TabularData";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { AuthenticationAndAuthorizationOptions, RouteClaimsRequirementOptions } from "../../Models/Ocelot/FileRoute";
import AuthenticationOptionsComponent from "./AuthenticationOptionsComponent";
import { Dictionary, DictionaryToKeyValuePairs, KeyValuePairsToDictionary } from "../../Models/Ocelot/KeyValuePair";

const normalizeData=(data:RouteClaimsRequirementOptions) =>{
    return DictionaryToKeyValuePairs(data as Dictionary<string>);
}

export default function AuthorizationComponent(props: BaseProps<AuthenticationAndAuthorizationOptions>){    
    const [show, setShow] = useState(false);
    const [authData, setAuthData] = useState(props.data?.AuthenticationOptions);
    const [claimData, setClaimData] = useState(normalizeData(props.data?.RouteClaimsRequirement??{}));
    const handleClose = () => {
        setAuthData(props.data?.AuthenticationOptions);
        setClaimData(normalizeData(props.data?.RouteClaimsRequirement??{}));
        setShow(false); 
    }
    const handleShow = () => setShow(true);
    const emitChange  = ()=> {
        if(props.onChange){
            props.onChange({
                ...props.data,
                AuthenticationOptions: authData,
                RouteClaimsRequirement: KeyValuePairsToDictionary(claimData)
            });
        }
        setShow(false);
    }
    const onAuthOptChange = (key: string, val:any) => {
        setAuthData({
            ...props.data?.AuthenticationOptions,
            [key]:val
        });
    }
    const onAdd = () => {
        let item = {...authData??{}};
        if(!item)
            item ={};
        if(!item?.AllowedScopes)
            item.AllowedScopes =[];
        item.AllowedScopes.push("");
        setAuthData(item);
     }
     const onRemove = (i:number) => {
        let item = {...authData ?? {}}
        if(!item?.AllowedScopes)
            return;
        item.AllowedScopes?.splice(i,1);
        setAuthData(item);
     }

     const onItemChange = (i:number, val:string) => {
        let item ={... authData ?? {}};
        if(!item?.AllowedScopes)
            return;
        item.AllowedScopes[i] = val;
        setAuthData(item)
     }

     const onAddClaim = () => {
        setClaimData([...claimData, {Key: "",Value:""}]);
     }
     const onRemoveClaim = (i:number) => {
        let items = [...claimData];
        items.splice(i,1);
        setClaimData(items);
     }
     const onItemChangeClaim = (i:number, key:string, value:string) => {
        let items = [...claimData];
        if(key==='col1'){
            items[i].Key = value;
        } else {
            items[i].Value = value;
        }
        setClaimData(items);
     }
    return (
        <>
        <Button variant="outline-light" className="border-0 m-0"  onClick={()=> handleShow()}>
            <FontAwesomeIcon className="text-success" icon={icon({name:'lock'})}></FontAwesomeIcon>
        </Button>
        <Modal show={show}  onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title>Authentication</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <AuthenticationOptionsComponent 
                    data={authData}
                    onDataChange={onAuthOptChange}
                    onAdd={onAdd}
                    onItemChange={onItemChange}
                    onRemove={onRemove}
                    />
                <TabularData 
                placeholder1="claim type" placeholder2="claim value"
                data={claimData} title="Route claims requirement"
                onAdd={onAddClaim}
                onRemove={onRemoveClaim}
                onItemChange={onItemChangeClaim}
                 />
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={handleClose}>
                    Cancel
                </Button>
                <Button variant="primary" onClick={emitChange}>
                    Ok
                </Button>
            </Modal.Footer>
        </Modal>
        </>
    )
}