import { Accordion, Badge, Col, Stack } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { icon } from "@fortawesome/fontawesome-svg-core/import.macro";
import { Colors, ShortName } from "../../Models/Colors";
import FileRouteComponent, { FileRouteComponentProps } from "./FileRouteComponent";

export default function FileRoutesComponent(props: FileRouteComponentProps) {
    let types = props.data?.UpstreamHttpMethod ?? [];
    let variant = types.length === 1 ? Colors[types[0]] : "mix";
    return (
        <Accordion.Item eventKey={props.data?.__id ?? "0"}>
            <Accordion.Header className={`p-0 acc  header-${variant}`}>
                <Col className="p-3">
                    <span className="path">
                        {props.data?.UpstreamPathTemplate}
                        <FontAwesomeIcon className="ms-3 me-3" icon={icon({ name: 'long-arrow-right' })}></FontAwesomeIcon>
                        {props.data?.DownstreamPathTemplate}
                    </span>
                </Col>
                <Col lg="4" className="me-5 p-0">
                    <Stack direction="horizontal" gap={2}>
                        {props.data?.Key  &&  <Badge id={props.data?.Key} className="me-auto fs-6"  pill bg="danger">{props.data?.Key}</Badge>}
                        {types.map((val, i) => (
                            <Badge className={i === 0 ? "ms-auto fs-6" : "fs-6"} key={i}  bg={Colors[val]}>{ShortName[val]}</Badge>
                        ))}
                    </Stack>
                </Col>
            </Accordion.Header>
            <Accordion.Body  className="d-flex align-items-start justify-content-between">
                <FileRouteComponent {...props} showDeleteButton={true} />
            </Accordion.Body>
        </Accordion.Item>
    );
}
