import { icon } from "@fortawesome/fontawesome-svg-core/import.macro";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState } from "react";
import { Button, Form, FormLabel, Modal } from "react-bootstrap";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { QoSOptions } from "../../Models/Ocelot/QoSOptions";

export default function QosOptionsModalComponent(props:BaseProps<QoSOptions>){
    const [show, setShow] = useState(false);
    const [opt, setOpt] = useState(props.data);
    const handleClose = () => {
        setOpt(props.data);
        setShow(false);
    }
    const handleShow = () => setShow(true);
    const onChange = (val:QoSOptions) => setOpt(val);
    const emitChange = () => {
        props.onChange &&  props.onChange(opt??{});
        setShow(false);
    }
    return (
        <>
            <Button variant="outline-light" className="border-0" onClick={()=>handleShow()}>
                <FontAwesomeIcon className="fs-5 text-success" icon={icon({ name: 'chain-broken' })}></FontAwesomeIcon>
            </Button>
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>QOS Options</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <QosOptionsComponent data={opt} onChange={onChange}/>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Cancel
                    </Button>
                    <Button variant="primary" onClick={emitChange}>
                        Ok
                    </Button>
                    
                </Modal.Footer>
            </Modal>
        </>
    )
}

export function QosOptionsComponent(props:BaseProps<QoSOptions>) {
    const onChange = (key:string, val:string) => {
        if(props.onChange){
            props.onChange({
                ...props.data,
                [key]:val
            })
        }
    }

    return (
        <>
            <FormLabel>ExceptionsAllowedBeforeBreaking</FormLabel>
            <Form.Control type="number" value={props.data?.ExceptionsAllowedBeforeBreaking ?? ""} 
                onChange={(e)=> onChange('ExceptionsAllowedBeforeBreaking',e.target.value)} ></Form.Control>
            <FormLabel className="mt-2">DurationOfBreak</FormLabel>
            <Form.Control type="number" value={props.data?.DurationOfBreak ?? ""}
                onChange={(e)=> onChange('DurationOfBreak',e.target.value)} ></Form.Control>
            <FormLabel className="mt-2">TimeoutValue</FormLabel>
            <Form.Control type="number" value={props.data?.TimeoutValue ?? ""}
                onChange={(e)=> onChange('TimeoutValue',e.target.value)} ></Form.Control>
        </>
    )
}