import { Card, Form, InputGroup } from "react-bootstrap";
import { FileAuthenticationOptions } from "../../Models/Ocelot/FileAuthenticationOptions";
import ArrayControl from "../Common/ArrayControl";
import { BaseProps, OnChange } from "../../Models/OcelotUi/BaseProps";

export default function AuthenticationOptionsComponent(props:BaseProps<FileAuthenticationOptions> & {
    onRemove: OnChange<number>,
    onAdd: OnChange<void>,
    onItemChange: (i:number, val:string)=>void,
    onDataChange: (key:string, val:any)=>void
    }
     ) { 
    return (
        <Card className="mb-3">
            <Card.Header className="d-flex align-items-center justify-content-between">
                <Card.Title as="h6" className="mb-0 ">Authentication options</Card.Title>
            </Card.Header>
            <Card.Body className="">
                <InputGroup  className="mb-3" >
                    <InputGroup.Text>AuthenticationProviderKey</InputGroup.Text>
                    <Form.Control placeholder=""  value={props.data?.AuthenticationProviderKey ?? ""}
                    onChange={(e)=>props.onDataChange('AuthenticationProviderKey',e.target.value)}
                    />
                </InputGroup>
                <ArrayControl title="Allowed scopes" controlType="text" placeholder="enter scope"  data={props.data?.AllowedScopes}
                onDelete={props.onRemove}
                onAdd={props.onAdd}
                onChange={props.onItemChange}
                 />
            </Card.Body>
        </Card>
    )
}
