import { Accordion, Col, Form, FormLabel, Row } from "react-bootstrap";
import { ReactNode } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { icon } from "@fortawesome/fontawesome-svg-core/import.macro";
import { IconProp } from "@fortawesome/fontawesome-svg-core";
import { FileGlobalConfiguration } from "../../Models/Ocelot/FileGlobalConfiguration";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import RateLimitOptionsComponent from "./RateLimitOptionsComponent";
import ServiceDiscoveryProviderComponent from "./ServiceDiscoveryProviderComponent";
import { QosOptionsComponent } from "./QosOptionsComponent";
import LoadBalancerOptionsComponent from "./LoadBalancerOptionsComponent";



export default function FileGlobalConfigurationComponent(props: BaseProps<FileGlobalConfiguration>) {
    const onChange = (key: string, val: any) => {
        props.onChange && props.onChange({
            ...props.data,
            [key]: val
        });
    }
    return (
       <>
            <Row>
                <Col>
                    <FormLabel>Base url </FormLabel>
                    <Form.Control placeholder="" value={props.data?.BaseUrl ?? ""}
                        onChange={(e) => onChange('BaseUrl', e.target.value)} />
                </Col>
                <Col>
                    <FormLabel>Request Id key </FormLabel>
                    <Form.Control placeholder="" value={props.data?.RequestIdKey ?? ""}
                        onChange={(e) => onChange('RequestIdKey', e.target.value)} />
                </Col>
                <Col>
                    <FormLabel >Downstream scheme </FormLabel>
                    <Form.Control placeholder="" value={props.data?.DownstreamScheme ?? ""}
                        onChange={(e) => onChange('DownstreamScheme', e.target.value)}
                    />
                </Col>
                <Col>
                    <FormLabel >Downstream http version </FormLabel>
                    <Form.Control placeholder="" value={props.data?.DownstreamHttpVersion ?? ""}
                        onChange={(e) => onChange('DownstreamHttpVersion', e.target.value)} />
                </Col>
            </Row>
            <Row className="mt-3">
                <Accord className="text-success" title="QOS" eventKey="qos" iconName={icon({ name: 'chain-broken' })}>
                    <QosOptionsComponent data={props.data?.QoSOptions}
                        onChange={(e) => onChange('QoSOptions', e)} />
                </Accord>
                <Accord className="text-danger" title="Rate limit" eventKey="rate" iconName={icon({ name: 'gauge-high' })}>
                    <RateLimitOptionsComponent data={props.data?.RateLimitOptions}
                        onChange={(e) => onChange('RateLimitOptions', e)} />
                </Accord>
                <Accord className="text-success" title="Load balancer" eventKey="load" iconName={icon({ name: 'balance-scale' })}>
                    <LoadBalancerOptionsComponent data={props.data?.LoadBalancerOptions}
                        onChange={(e) => onChange('LoadBalancerOptions', e)} />
                </Accord>
                <Accord className="text-info" title="Service discovery" eventKey="discovery" iconName={icon({ name: 'globe' })}>
                    <ServiceDiscoveryProviderComponent data={props.data?.ServiceDiscoveryProvider}
                        onChange={(e) => onChange('ServiceDiscoveryProvider', e)} />
                </Accord>
            </Row>
           </>
    )
}

function Accord(props: { children: ReactNode, eventKey: string, title: string, className: string, iconName: IconProp }) {
    return (
        <Col >
            <Accordion>
                <Accordion.Item eventKey={props.eventKey}>
                    <Accordion.Header>
                        <FontAwesomeIcon className={`${props.className} me-3 fs-4`} icon={props.iconName}></FontAwesomeIcon>{props.title}
                    </Accordion.Header>
                    <Accordion.Body>
                        {props.children}
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
        </Col>
    )
}
