import { Button, Modal } from "react-bootstrap";
import TabularData from "../Common/TabularData";
import { useState } from "react";
import { Arrow } from "../Common/Arrow";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { DownstreamTransformationOptions } from "../../Models/Ocelot/FileRoute";
import { Dictionary, DictionaryToKeyValuePairs, KeyValuePairsToDictionary } from "../../Models/Ocelot/KeyValuePair";

const normalizeData=(data?:Dictionary<string>) =>DictionaryToKeyValuePairs(data??{});

export default function ResponseTransformationComponent(props: BaseProps<DownstreamTransformationOptions>) {
    const [transform, setTransform] = useState(normalizeData(props.data?.DownstreamHeaderTransform));
     const [show, setShow] = useState(false);
     const handleClose = () => {
        setTransform(normalizeData(props.data?.DownstreamHeaderTransform));
        setShow(false); 
    }
    const handleShow = () => setShow(true);
    const emitChange  = ()=> {
        if(props.onChange){
            props.onChange({
                ...props.data,
                DownstreamHeaderTransform: KeyValuePairsToDictionary(transform)
            });
        }
        setShow(false);
    }

    const onAdd = () => {
        setTransform([...transform, {Key: "",Value:""}]);
     }
     const onRemove = (i:number) => {
        let items = [...transform];
        items.splice(i,1);
        setTransform(items);
     }
     const onItemChange = (i:number, key:string, value:string) => {
        let items = [...transform];
        if(key==='col1'){
            items[i].Key = value;
        } else {
            items[i].Value = value;
        }
        setTransform(items);
     }

    return (
        <>
        <Arrow direction="left" className="mt-4 mb-5" >
            <button className="btn border-0" onClick={()=>handleShow()}>Response</button>
        </Arrow>
        <Modal show={show}  onHide={handleClose}>
            <Modal.Header closeButton>
                <Modal.Title>Response transformation</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <TabularData data={transform} title="Downstream Header transform"
                onAdd={onAdd}
                onItemChange={onItemChange}
                onRemove={onRemove}
                placeholder1="header name"
                placeholder2="transformation"
                
                 />
            </Modal.Body>
            <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Cancel
                    </Button>
                    <Button variant="primary" onClick={emitChange}>
                        Ok
                    </Button>
            </Modal.Footer>
        </Modal>
        </>


    )
}