import { Accordion, Container, Navbar } from "react-bootstrap";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import FileGlobalConfigurationComponent from "./FileGlobalConfigurationComponent";
import { FileConfiguration } from "../../Models/Ocelot/FileConfiguration";
import { FileRoute } from "../../Models/Ocelot/FileRoute";
import AddRouteComponent from "./AddRouteComponent";
import {Idify} from "../../Models/Utilities"
import FileAggregateRouteComponent from "./FileAggregateRouteComponent";
import AddAggregateRouteComponent from "./AddAggregateRouteComponent";
import FileRoutesComponent from "./FileRoutesComponent";

export default function OcelotComponent(props: BaseProps<FileConfiguration>) {
    const onChange = (key: string, val: any) => {
        props.onChange &&  props.onChange({ ...props.data, [key]: val });
    }

    const onFileRouteDelete = (id: string) => {
        let route = [...props.data?.Routes ?? []].filter(x => x.__id !== id);
        props.onChange && props.onChange({
                ...props.data,
                Routes: route
        });
    }
    const onFileRouteChange = (e: { id: string, route: FileRoute }) => {
        let x = { ...props.data };
        x.Routes = (x.Routes ?? []).map(x => {
            if (x.__id === e.id) {
                return e.route;
            }
            return x;
        });
        props.onChange && props.onChange(x);
    }

    const onAdd = (e:FileRoute) => {
        let x = { ...props.data };
        if(!x.Routes)
            x.Routes = [];
        x.Routes.push(e);
        props.onChange && props.onChange(
            Idify(x)
        )
    }

    return (
        <>
            <Container className="bg-body">
                <Accordion className="mb-3 w-100 bg-body" defaultActiveKey={"global"}>
                    <Accordion.Item eventKey={"global"} >
                        <Accordion.Header>
                            <h5>Global configuration</h5>
                        </Accordion.Header>
                        <Accordion.Body>
                            <FileGlobalConfigurationComponent data={props.data?.GlobalConfiguration}
                                onChange={(e) => onChange('GlobalConfiguration', e)}
                            />
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <Navbar sticky="top" className="bg-body">
                    <div className="d-flex flex-column w-100 bg-body  border-bottom p-0">
                        <div className="d-flex justify-content-end mb-2 pb-0">
                            <AddRouteComponent data={{
                                DownstreamHostAndPorts:[]
                            }} onChange={onAdd}/>
                        </div>
                    </div>
                </Navbar>
                <Accordion className="mb-3" defaultActiveKey={"routes"} >
                    <Accordion.Item eventKey={"routes"}>
                        <Accordion.Header>
                            <h5>Routes</h5>
                        </Accordion.Header>
                        <Accordion.Body>
                            <Accordion  alwaysOpen >
                            {
                                (props.data?.Routes ?? []).map((x, i) => (<FileRoutesComponent showDeleteButton={true} key={i} data={x}
                                    onDelete={() => onFileRouteDelete(x.__id ?? "0")}
                                    onChange={(e) => onFileRouteChange({id: x.__id ?? "0", route:e})} />)
                                )
                            }
                            </Accordion>
                            { props.data?.Routes?.length == 0 && <h6>No routes defined</h6> }
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                {/* <Navbar sticky="top" className="bg-body">
                    <div className="d-flex flex-column w-100 bg-body  border-bottom p-0">
                        <div className="d-flex justify-content-end mb-2 pb-0">
                            <AddAggregateRouteComponent />
                        </div>
                    </div>
                </Navbar> */}
                {/* <Accordion defaultActiveKey={"aggregates"}>
                    <Accordion.Item eventKey={"aggregates"} >
                        <Accordion.Header>
                            <h5>Aggregates</h5>
                        </Accordion.Header>
                        <Accordion.Body>
                            {(props.data?.Aggregates??[]).map((x,i)=> (
                                <FileAggregateRouteComponent showDeleteButton={true} key={i} />
                            ))}
                            { !props.data?.Aggregates?.length && <h6>No aggregate defined</h6> }
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion> */}
            </Container>
        </>
    )
}

