import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Button, Modal } from "react-bootstrap";
import { icon } from "@fortawesome/fontawesome-svg-core/import.macro";
import { useState } from "react";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { SecurityOptions } from "../../Models/Ocelot/SecurityOptions";
import ArrayControl from "../Common/ArrayControl";

export default function SecurityOptionsComponent(props:BaseProps<SecurityOptions>){
    const [allowed,setAllowed] = useState(props.data?.IPAllowedList);
    const [blocked,setBlocked] = useState(props.data?.IPBlockedList);
    const [show, setShow] = useState(false);
    const handleClose = () => {
        setAllowed(props.data?.IPAllowedList);
        setBlocked(props.data?.IPBlockedList);
        setShow(false);
    }
    const handleShow = () => setShow(true);

    const onAddAllowed = () => {
        let items = [...allowed ??[]];
        items.push("");
        setAllowed(items);
     }
     const onRemoveAllowed = (i:number) => {
        let items = [...allowed ??[]];
        items.splice(i,1);
        setAllowed(items);
     }

     const onItemChangeAllowed = (i:number, val:string) => {
        let items = [...allowed ??[]];
        items[i] = val;
        setAllowed(items);
     }

     const onAddBlocked = () => {
        let items = [...blocked ??[]];
        items.push("");
        setBlocked(items);
     }
     const onRemoveBlocked = (i:number) => {
        let items = [...blocked ??[]];
        items.splice(i,1);
        setBlocked(items);
     }

     const onItemChangeBlocked = (i:number, val:string) => {
        let items = [...blocked ??[]];
        items[i] = val;
        setBlocked(items);
     }
     const emitChange=()=>{
        props.onChange && props.onChange({
            IPAllowedList: allowed,
            IPBlockedList: blocked
        });
        setShow(false);
     }
    return (
        <>
            <Button  variant="outline-light" className="border-0 m-0 text-danger" onClick={()=>handleShow()} >
                <FontAwesomeIcon icon={icon({name:'shield'})} />
            </Button>
            <Modal show={show}  onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Security options</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <ArrayControl data={allowed} 
                    title="IP Allowed List"
                    placeholder="enter ip address to while list"
                    controlType="ipAddress"
                    onAdd={onAddAllowed}
                    onDelete={onRemoveAllowed}
                    onChange={onItemChangeAllowed}
                    />
                    <ArrayControl data={blocked} title="IP Blocked List"
                    placeholder="enter ip address to black list"
                    controlType="ipAddress"
                    onAdd={onAddBlocked}
                    onDelete={onRemoveBlocked}
                    onChange={onItemChangeBlocked}
                    />
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={emitChange}>
                        Ok
                    </Button>
                    
                </Modal.Footer>
            </Modal>
        </>
    )
}