import { useState } from "react";
import { Alert, Button, Col, Form, Modal, Row } from "react-bootstrap";
import { FileConfiguration } from "../../Models/Ocelot/FileConfiguration";


export default function Toolbar(props:{onImport:Function, value?:FileConfiguration, onClear:Function, onExport:Function}){
    const [json, setJson] = useState(JSON.stringify(props.value??{}, null, '    '));
    const [error, setError] = useState('');
    const [show, setShow] = useState(false);
    const [title, setTitle] = useState('');
    const handleClose = () => setShow(false);
    
    const handleShow = (type:string) => {
        if(type==='import')
        {
            setJson('');
            setTitle("Paste your ocelot json here")
        }
        else
        {
            setJson(JSON.stringify(props.value??{}, null, '    '));
            setTitle("Ocelot json");
        }
        setError('');
        setShow(true);
    }
    const LoadJson = () => {
        setError('')
        try {
            let parseJson = JSON.parse(json) as FileConfiguration;
            props.onImport(parseJson);
            setShow(false);
        }
        catch(e:any){
            setError(`Invalid json ${e.toString()}`);
        }
    }
    const clearAll = () => {
        props.onClear();
    }
    return (
        <>
            <a className="btn btn-link" onClick={()=>clearAll()}>Clear</a>
            <a className="btn btn-link" onClick={()=>handleShow('export')}>Export json</a>
            <a className="btn btn-link" onClick={()=>handleShow('import')}>Import json</a>
            <Modal show={show} size="xl"  onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>{title}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                {error && <Alert variant="danger">{error}</Alert>}
                    <Form.Control as="textarea" value={json} rows={20} onChange={(e)=>setJson(e.target.value)} />
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Cancel
                    </Button>
                    {
                    title ==='Paste your ocelot json here' &&
                    <Button variant="primary" onClick={LoadJson}>
                        Load
                    </Button>
                    }
                </Modal.Footer>
            </Modal>

        </>
    )
}