import { useEffect, useState } from 'react';
import './App.css';
import { Badge, Col, Container, Nav, Navbar, Row } from 'react-bootstrap';
import { Idify } from './Models/Utilities';
import Toolbar from './Components/Common/Toolbar';
import ThemeButton from './Components/Common/ThemeButton';
import OcelotComponent from './Components/OcelotUi/OcelotComponent';
import { BaseProps } from './Models/OcelotUi/BaseProps';
import { FileConfiguration, FileConfigurationMetadata } from './Models/Ocelot/FileConfiguration';
import ReactMarkdown from 'react-markdown'

function App() {
  const [json, setJson] = useState<FileConfiguration>({});
  useEffect(()=> {
    fetch("/sample.json").then(res=>res.json())
    .then(json=> setJson(Idify(json)));
  }, [])
  const onImport = (e: FileConfiguration) => {
    setJson(Idify(e));
  }
  const onClear = () => {
    setJson({})
  }
  const onExport = () => {

  }
  return (
    <Container fluid className='p-0'>
      <Navbar expand="lg" className="top-nav mb-3">
        <Container >
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Item as="h1">{process.env.REACT_APP_TITLE}</Nav.Item>
            </Nav>
            <Nav.Item className="justify-content-end">
              <Toolbar value={json} onImport={onImport} onClear={onClear} onExport={onExport} />
              <ThemeButton />
            </Nav.Item>
          </Navbar.Collapse>
        </Container>
      </Navbar>
     
      <Container >
       <Message />
        <MetadataComponent data={json?.Metadata} />
        <Row className='mb-3'>
          <OcelotComponent data={json} onChange={onImport} />
        </Row>
        <Row className='mb-3'>
          <Col className='d-flex justify-content-center'>
            Developer: Ujjwal Manandhar
          </Col>
        </Row>
      </Container>
    </Container>

  );
}

function Message() {
  return (
    <>
    <div className='mb-5'>
      <h5>
        Ocelot JSON File Visualisation Console
      </h5>
      <p>
        This is a sample JSON file for Ocelot, which is loaded locally within the application. You can learn more about Ocelot by visiting their GitHub repository.
      </p>
      <p>
        The console allows you to visualize the Ocelot JSON, providing a broader understanding. You also have the ability to import your own Ocelot JSON, visualize it, make visual modifications, and then export the updated version. As part of this project, I plan to open source it and integrate the Asp.Net Core library, connecting it with the Administration library.
      </p>
      <p>
        While this project is a work in progress, it currently covers all the functionalities related to route and global configuration that I am aware of. I am actively working on further developing this project, and once it reaches a presentable state, I will release the source code as open source. Your feedback is highly valuable during this phase. Please feel free to share any suggestions and comments, as they serve as motivation for the project's advancement.
      </p>
      <p>
        You can access the JSON file in root folder

      </p>
      </div>

    </>
  )
}

function MetadataComponent(props: BaseProps<FileConfigurationMetadata>) {
  return (
    <div className='metadata'>
      {
        props.data?.Title &&
        <Row className='mb-3'>
          <h2>{props.data?.Title} </h2>
          <h4>{props.data?.Version && <Badge pill bg='success'>{props.data?.Version}</Badge>}</h4>
        </Row>
      }
      {
        props.data?.Description &&
        <Row className='mb-3'>
          
            <ReactMarkdown >
              {props.data?.Description}
            </ReactMarkdown>
          
        </Row>
      }
    </div>
  )
}
export default App;
