import { ButtonGroup, Card, Col, Form } from "react-bootstrap";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { AuthenticationAndAuthorizationOptions, FileRoute } from "../../Models/Ocelot/FileRoute";
import QosOptionsModalComponent from "./QosOptionsComponent";
import {LoadBalancerOptionsModalComponent} from "./LoadBalancerOptionsComponent";
import RateLimitRuleModalComponent from "./RateLimitRuleComponent";
import HttpMethodComponent from "./HttpMethodComponent";
import HttpHandlerOptionsComponent from "./HttpHandlerOptionsComponent";
import AuthorizationComponent from "./AuthorizationComponent";
import SecurityOptionsComponent from "./SecurityOptionsComponent";

export default function UpstreamComponent(props: BaseProps<FileRoute>) {
    const onChange = (key: string, val:any) => {
        if(key === ''){
            props.onChange && props.onChange({
                ...props.data,
                ...val
            })
        } else {
            props.onChange && props.onChange({
                ...props.data,
                [key]: val
            });
        }
    }
   
    return (
        <>
            <Card>
                <Card.Header className="p-0">
                    <Card.Title className="d-flex align-items-center m-0 ps-2">
                        Upstream
                        <SecurityOptionsComponent data={props.data?.SecurityOptions} onChange={(e)=> onChange('SecurityOptions',e)}/>
                        <AuthorizationComponent data={props.data} onChange={(e)=>onChange('',e)} />
                        <HttpHandlerOptionsComponent  data={props.data?.HttpHandlerOptions} onChange={(e)=>onChange('HttpHandlerOptions',e)}/>
                    </Card.Title>
                </Card.Header>
                <Card.Body className="d-flex p-0">
                    <Col className="p-3">
                        <HttpMethodComponent data={props.data?.UpstreamHttpMethod} onChange={(e)=>onChange('UpstreamHttpMethod',e)} />
                        <Form.Control placeholder="Upstream path" value={props.data?.UpstreamPathTemplate ?? ""}
                            onChange={(e)=>onChange('UpstreamPathTemplate', e.target.value)} />
                    </Col>
                    <Col sm="auto" className="d-flex  align-items-between border-start">
                        <ButtonGroup vertical  >
                            <QosOptionsModalComponent data={props.data?.QoSOptions} onChange={e=>onChange('QoSOptions',e)}/>
                            <LoadBalancerOptionsModalComponent data={props.data?.LoadBalancerOptions} onChange={e=>onChange('LoadBalancerOptions',e)}/>
                            <RateLimitRuleModalComponent data={props.data?.RateLimitingOptions} onChange={e=>onChange('RateLimitingOptions',e)}/>
                        </ButtonGroup>
                    </Col>
                </Card.Body>
            </Card>
        </>
    )
}