import { icon } from "@fortawesome/fontawesome-svg-core/import.macro";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Button, Card, CloseButton, Form, InputGroup } from "react-bootstrap";

export type ArrayControlType = "text" | "ipAddress";
export default function ArrayControl(props:{
    title:string, 
    data?:string[] | null | undefined, 
    placeholder?: string, 
    controlType?: ArrayControlType,
    onChange?: (i:number, text:string) => void,
    onAdd? : () => void,
    onDelete? : (i:number) => void;
}){
    
    return (
        <Card className="p-0 mb-3">
            <Card.Header className="d-flex align-items-center justify-content-between">
                <Card.Title as="h6" className="mb-0 ">{props.title}</Card.Title>
                <Button size="sm" variant="success" onClick={props.onAdd}>
                    <FontAwesomeIcon icon={icon({ name: 'plus' })} />
                </Button>
            </Card.Header>
        <Card.Body className="p-0 m-0">
            { (props.data??[]).length > 0 && (props.data??[]).map((key,i)=>(
                                        <InputGroup key={i} className={`${i==0 ? "" : "border-top"} d-flex align-items-center`} >
                                            <Form.Control placeholder={props.placeholder} 
                                            className="border-0 border-end" value={key} 
                                            onChange={(e)=>{
                                                if(props.onChange){
                                                    props.onChange(i, e.target.value);
                                                }
                                            }}
                                            />
                                            <CloseButton className="mx-2"
                                            onClick={()=>{
                                                if(props.onDelete){
                                                    props.onDelete(i);
                                                }
                                            }}
                                              />
                                        </InputGroup>
                                    )
                        )
            }
            { (props.data??[]).length == 0 && <InputGroup className="px-3 py-1">No data</InputGroup> }
        </Card.Body>
    </Card>
    )
}
