import { icon } from "@fortawesome/fontawesome-svg-core/import.macro";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState } from "react";
import { Button, Col, Form, Modal } from "react-bootstrap";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { FileRoute } from "../../Models/Ocelot/FileRoute";
import FileRouteComponent from "./FileRouteComponent";

export default function AddRouteComponent(props:BaseProps<FileRoute>) {
    const [route, setRoute] = useState(props.data);
    const [show, setShow] = useState(false);
    const handleClose = () => {
        setRoute(props.data);
        setShow(false);
    }
    const handleShow = () => setShow(true);
    const onChange = (key:FileRoute) =>  setRoute(key)
    const emitChange = () => {
        props.onChange && props.onChange(route??{});
        setShow(false);
    }
    return (
        <>
            <Button variant="primary"  onClick={() => handleShow()}>
                <FontAwesomeIcon icon={icon({ name: 'plus-circle' })}></FontAwesomeIcon>
                &nbsp;Add route 
            </Button>
            <Modal show={show} onHide={handleClose} size="xl">
                <Modal.Header closeButton>
                    <Modal.Title>Add new route</Modal.Title>
                </Modal.Header>
                <Modal.Body className="d-flex">
                    <FileRouteComponent data={route} onChange={onChange} showDeleteButton={false} onDelete={function (): void {
                        
                    } } />
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Cancel
                    </Button>
                    <Button variant="primary" onClick={emitChange}>
                        Add
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}