import { Form, FormLabel } from "react-bootstrap";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { RateLimitOptions } from "../../Models/Ocelot/RateLimitOptions";

export default function RateLimitOptionsComponent(props:BaseProps<RateLimitOptions>){
    const onChange = (key:string, val:string|boolean) => {
        props.onChange &&
            props.onChange({
                ...props.data,
                [key]:val
            })
    }
    return (
        <>  
            <Form.Check type="switch" label="DisableRateLimitHeaders" 
               onChange={(e)=> onChange('DisableRateLimitHeaders',e.target.checked)} ></Form.Check>
            <FormLabel className="mt-2" >ClientIdHeader</FormLabel>
            <Form.Control value={props.data?.ClientIdHeader ?? ""} 
               onChange={(e)=> onChange('ClientIdHeader',e.target.value)} ></Form.Control>
            <FormLabel className="mt-2">QuotaExceededMessage</FormLabel>
            <Form.Control value={props.data?.QuotaExceededMessage ?? ""}  
               onChange={(e)=> onChange('QuotaExceededMessage',e.target.value)} ></Form.Control>
            <FormLabel className="mt-2">RateLimitCounterPrefix</FormLabel>
            <Form.Control value={props.data?.RateLimitCounterPrefix ?? ""}  
               onChange={(e)=> onChange('RateLimitCounterPrefix',e.target.value)} ></Form.Control>
            <FormLabel className="mt-2">HttpStatusCode</FormLabel>
            <Form.Control value={props.data?.HttpStatusCode ?? ""} type="number" 
               onChange={(e)=> onChange('HttpStatusCode',e.target.value)} ></Form.Control>
        </>
    )
}