import { Button, Modal } from "react-bootstrap";
import TabularData from "../Common/TabularData";
import { useState } from "react";
import { BaseProps } from "../../Models/OcelotUi/BaseProps";
import { UpstreamTransformationOptions } from "../../Models/Ocelot/FileRoute";
import { Arrow } from "../Common/Arrow";
import { Dictionary, DictionaryToKeyValuePairs, KeyValuePair, KeyValuePairsToDictionary } from "../../Models/Ocelot/KeyValuePair";

const normalizeData=(data?:Dictionary<string>) =>{
    return DictionaryToKeyValuePairs(data??{});
}

export default function RequestTransformationComponent(props: BaseProps<UpstreamTransformationOptions>) {
    const [transform, setTransform] = useState(normalizeData(props.data?.UpstreamHeaderTransform));
    const [header, setHeader] = useState(normalizeData(props.data?.AddHeadersToRequest));
    const [claim, setClaim] = useState(normalizeData(props.data?.AddClaimsToRequest));
    const [query, setQuery] = useState(normalizeData(props.data?.AddQueriesToRequest));
    const [show, setShow] = useState(false);
    const handleClose = () => {
        setTransform(normalizeData(props.data?.UpstreamHeaderTransform));
        setHeader(normalizeData(props.data?.UpstreamHeaderTransform));
        setClaim(normalizeData(props.data?.UpstreamHeaderTransform));
        setQuery(normalizeData(props.data?.UpstreamHeaderTransform));
        setShow(false); 
    }
    const handleShow = () => setShow(true);
    const emitChange  = ()=> {
        props.onChange &&
            props.onChange({
                ...props.data,
                UpstreamHeaderTransform: KeyValuePairsToDictionary(transform),
                AddHeadersToRequest: KeyValuePairsToDictionary(header),
                AddClaimsToRequest: KeyValuePairsToDictionary(claim),
                AddQueriesToRequest: KeyValuePairsToDictionary(query)
            });
        
        setShow(false);
    }

    const onAdd = () => setTransform([...transform, {Key: "",Value:""}]);
    const onRemove = (i:number) => {
        let items = [...transform];
        items.splice(i,1);
        setTransform(items);
     }
     const onItemChange = (i:number, key:string, value:string) => {
        let items = [...transform];
        let keyOrValue = key==='col1' ? 'Key' : 'Value'
        items[i] = {
            ...items[i],
            [keyOrValue]: value
        }
        setTransform(items);
     }

     const onAddHeader = () => {
        setHeader([...header, {Key: "",Value:""}]);
     }
     const onRemoveHeader = (i:number) => {
        let items = [...header];
        items.splice(i,1);
        setHeader(items);
     }
     const onItemChangeHeader = (i:number, key:string, value:string) => {
        let items = [...header];
        let keyOrValue = key==='col1' ? 'Key' : 'Value'
        items[i] = {
            ...items[i],
            [keyOrValue]: value
        }
        setHeader(items);
     }

     const onAddClaim = () =>  setClaim([...claim, {Key: "",Value:""}]);
     const onRemoveClaim = (i:number) => {
        let items = [...claim];
        items.splice(i,1);
        setClaim(items);
     }
     const onItemChangeClaim = (i:number, key:string, value:string) => {
        let items = [...claim];
        let keyOrValue = key==='col1' ? 'Key' : 'Value'
        items[i] = {
            ...items[i],
            [keyOrValue]: value
        }
        setClaim(items);
     }

     const onAddQuery = () => setQuery([...query, {Key: "",Value:""}]);
     const onRemoveQuery = (i:number) => {
        let items = [...query];
        items.splice(i,1);
        setQuery(items);
     }
     const onItemChangeQuery = (i:number, key:string, value:string) => {
        let items = [...query];
        let keyOrValue = key==='col1' ? 'Key' : 'Value'
        items[i] = {
            ...items[i],
            [keyOrValue]: value
        }
        setQuery(items);
     }

    return (
        <>
            <Arrow direction="right" className="mt-4 mb-5" >
                <button className="btn border-0" onClick={() => handleShow()}>Request</button>
            </Arrow>
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Request transformation</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <TabularData data={transform} title="Upstream Header transform" 
                    onAdd={onAdd}
                    onItemChange={onItemChange}
                    onRemove={onRemove}
                    placeholder1="header name"
                    placeholder2="transformation"
                    
                    />
                    <TabularData data={claim} title="Add claims to request"
                    onAdd={onAddClaim}
                    onItemChange={onItemChangeClaim}
                    onRemove={onRemoveClaim}
                    placeholder1="claim type"
                    placeholder2="transformation"
                     />
                    <TabularData data={header} title="Add headers to request" 
                    onAdd={onAddHeader}
                    onItemChange={onItemChangeHeader}
                    onRemove={onRemoveHeader}
                    placeholder1="header name"
                    placeholder2="transformation"
                    />
                    <TabularData data={query} title="Add queries to request"
                    onAdd={onAddQuery}
                    onItemChange={onItemChangeQuery}
                    onRemove={onRemoveQuery}
                    placeholder1="query name"
                    placeholder2="transformation"
                     />
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Cancel
                    </Button>
                    <Button variant="primary" onClick={emitChange}>
                        Ok
                    </Button>
            </Modal.Footer>
            </Modal>
        </>
    )
}